  splice = [].splice;

import React from 'react';

import "./Modals.css";

({Fragment, Component} = React);

import _urls from "./urls.js";

g_container = null;

export var Container = class Container extends Component {
  constructor() {
    super(...arguments);
    this.pop = this.pop.bind(this);
    this.stack = [];
  }

  componentDidMount() {
    return g_container = this;
  }

  render() {
    var modal;
    modal = this.stack[this.stack.length - 1];
    return <div className="modals">{modal && <Fragment>
        <div className="backdrop" onClick={this.pop} />
        <div className="modal">
          {modal}
        </div>
      </Fragment>}
    </div>;
  }

  push(modal) {
    this.stack.push(modal);
    return this.forceUpdate();
  }

  pop() {
    boundMethodCheck(this, Container);
    this.stack.pop();
    return this.forceUpdate();
  }

};

export var push = function(modal) {
  return g_container.push(modal);
};

export var pop = function() {
  return g_container.pop();
};

export var PreviewModal = class PreviewModal extends Component {
  constructor() {
    super(...arguments);
    this.state = {
      index: 0
    };
  }

  getArrowClickHandler(prev) {
    return (ev) => {
      ev.preventDefault();
      return this.setState(function(oldState) {
        return {
          index: modulo(oldState.index + (prev ? -1 : 1), 3)
        };
      });
    };
  }

  tryLowerCaseExtention(ev) {
    var ext, ref, rest;
    ref = ev.target.src.split("."), [...rest] = ref, [ext] = splice.call(rest, -1);
    if (ext === (ext = ext.toLowerCase())) {
      return;
    }
    return ev.target.src = [...rest, ext].join(".");
  }

  render() {
    var image, name, url;
    ({name} = this.props);
    image = ["Chat", "Friends", "Settings"][this.state.index];
    url = `${_urls.previewFiles}${name.split(" ").join("_")}/${image}.PNG?raw=true`;
    return <div className="preview-modal">
      <div className="left" onClick={this.getArrowClickHandler(true)} />
      <div className="spinner" />
      <img src={url} alt={name} onClick={g_container.pop} onError={this.tryLowerCaseExtention} />
      <div className="right" onClick={this.getArrowClickHandler(false)} />
    </div>;
  }

};



